.hamburger {
  display: none;
}

.hamburgerX {
  display: none;
}

@media screen and (max-width: 760px) {
  .hamburger {
    padding-top: 15px;
    padding-right: 20px;
    display: block;
    cursor: pointer;
  }

  .hamburger div {
    width: 30px;
    height: 2.3px;
    background-color: rgb(255, 255, 255);
    color: white;
    margin-bottom: 3.5px;
    transition: all 300ms ease-in-out;
  }

  .hamburgerX {
    padding-top: 15px;
    padding-right: 20px;
    display: block;
    cursor: pointer;
  }

  .hamburgerX div {
    width: 40px;
    height: 2.3px;
    background-color: rgb(255, 255, 255);
    color: white;
    margin-bottom: 3.5px;
  }

  .hamburgerX div:nth-child(1) {
    transform: rotate(45deg);
    transition: all 300ms ease-in-out;
  }

  .hamburgerX div:nth-child(3) {
    transform: rotate(-45deg);
    transition: all 300ms ease-in-out;
  }

  .hamburgerX div:nth-child(2) {
  
    display: none;
    transition: all 300ms ease-in-out;
  }
}

.nav-tile {
  height: 80px;
  width: 100%;
  background-color: rgb(1, 7, 34);
  background-color: rgb(0, 0, 0);
  display: flex;
  justify-content: space-between;
  color: rgb(255, 255, 255);
  
}

.nav-holder {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-top: 15px;
  z-index: 3;
}

.menu-tile {
  display: flex;
  align-items: stretch;
  font-size: 1.5rem;
  padding-top: 10px;
  padding-right: 45px;
  padding-bottom: 20px;
    font-family: 'Manjari', sans-serif;
}
.menu-tile li {
  display: flex;
  justify-content: space-between;
  margin-left: 20px;
  text-decoration: none;
}

.menu-tile > li > a {
  text-decoration: none;
  color: #d5e9e8;
}

.company-name {
  padding-top: 10px;
  padding-left: 16px;
  font-family: 'Manjari', sans-serif;
  font-size: 34px;
  cursor: pointer;
}


.nav-links:after {
  content: "";
  display: block;
  margin: 0 auto;
  width: 0%;
  padding-top: 10px;
  border-bottom: 4px solid rgb(255, 255, 255);
}


.nav-links:hover::after {
  content: "";
  display: block;
  margin: 0 auto;
  width: 100%;
  padding-top: 10px;
  border-bottom: 4px solid rgb(255, 255, 255);
  transition: all 200ms ease-in-out;
    
}



@media screen and (max-width: 760px) {
 
  .nav-tile{
    /* position: fixed; */
    z-index: 10;
  }

  .nav-holder {
    position: relative;
  }

  .menu-tile {
    display: flex;
    position: absolute;
    flex-direction: column;
    width: 100%;
    top: -600px;
    padding: 0;
    background-color: black;
    background-color: rgb(1, 7, 34);
    transition: all 400ms ease-in-out;
  }
  .active {
    display: flex;
    position: absolute;
    flex-direction: column;
    width: 100%;
    top: 60px;
    left: 0px;
    padding: 0;
    background-color: black;
    /* background-color: rgb(1, 7, 34); */
    transition: all 400ms ease-in-out;
    z-index: 2;
    border-bottom: 1px solid rgb(78, 74, 74);
  }

  .menu-tile > li {
    padding: 10px;
    margin: 0;
    text-decoration: none;
    color: rgb(0, 255, 55);
    width: 100%;
  }

  .menu-tile > li > a {
    margin: 0 auto;
  }
}




