.main-heading {
  margin-top: 0px;
  margin-bottom: 10px;
  font-size: 40px;
  cursor: pointer;
  text-align: center;
}


.main-heading:after {
  content: "";
  display: block;
  margin: 0 auto;
  width: 10%;
  padding-top: 10px;
  border-bottom: 4px solid black;
}
.main-heading:before {
  content: "";
  display: block;
  margin: 0 auto 5px;
  width: 10%;
  padding-bottom: 10px;
  border-bottom: 4px solid black;
}

.main-heading:hover::after {
  width: 5%;
  transition: all 0.3s 0s ease-in-out;
}
.main-heading:hover::before {
  width: 5%;
  transition: all 0.3s 0s ease-in-out;
}
