    position: relative;
  }

  .modal-card-child > .modal-product-image {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 0%;
    right: 0%;
    height: 100%;
  }
  .modal-card-child > .modal-description {
    position: absolute;
    top: 85%;
    left: 0%;
    right: 0%;
    height: 100%;
    overflow-y: scroll;
    margin: 0;
  }

  .modal-product-type {
    position: absolute;
    top: 12%;
    margin-right: 20px;
    margin-left: 20px;
    font-size: 10px;
  }

  .modal-product-description {
    position: absolute;
    top: 18%;
    margin-right: 40px;
    margin-left: 20px;
    font-size: 14px;
    margin-top: 30px;
    word-break: normal;
    font-style: italic;
    overflow-y: scroll;
    height: 280px;
    background-color: #494747;
    padding: 20px;
  }

  .modal-product-color {
    position: absolute;
    top: 86%;
    margin-left: 20px;
  }
}

@media screen and (max-height: 599px) and (orientation: landscape) {
  .modal-card {
    height: 350px;
    top: 0%;
    /* bottom: 0%; */
  }

  .modal-product-description {
    height: 130px;
  }

  .modal-product-color {
    top: 75%;
  }
}

@media screen and (min-width: 408px) and (max-width: 418px) and (orientation: portrait) {
  .modal-product-description {
    height: 300px;
  }
}




